import React,{ PropTypes } from 'react'
import {render}     from 'react-dom';
import { Router, Route, hashHistory,browserHistory, IndexRoute } from 'react-router';
import Topheader    from '../components/header';
import Footer       from '../components/footer';
import Page         from '../components/pages/page';
import {Login,auth} from '../components/pages/login';
import Chartjs      from '../components/pages/charts';
import Sidebar      from '../components/sidebar';
import { Rightctrlsidebar } from '../components/sidebar/rightctrlsidebar';
import About  from '../components/pages/about';
import App from   '../components/pages/App';
import Repos from '../components/pages/Repos';
import Repo from  '../components/pages/Repo';
import Home from  '../components/pages/Home';
import Uibutton  from '../components/pages/uibutton';
import menuss  from './menus';

let menus = menuss.children;

const Main = (props) => {
  console.log(props);
  let chkauth = auth.loggedIn();
  if(chkauth){
          let menu = Object.assign({}, menus);
          console.log('menu=',menu);
          return <div>
                  <Topheader menus={menu}/>
                  <Sidebar menus={menu}/>
                  <div className="content-wrapper">
                   {props.children}
                   </div>
                  <Footer />
                  <Rightctrlsidebar />
                  <div className="control-sidebar-bg"></div>
             </div>
  } else {
      return (<div><Login {...props} /></div>)
  }
}


export default Main



